.listOfTodos {
  max-height: 280px;
  height: 250px;
  width: 280px;
  width: 350px;
  overflow-y: scroll;
    background-color: rgba(121, 214, 113, 0);
  
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;  
  margin-top: 25px;
  padding: 10px 0 10px 0;
  list-style: none;
}

.empty{
  padding:10px;
  font-size: 30px;
  color: brown; 
  text-align: center;
  font-family: 'Patrick Hand SC', cursive;
}

.toggledEmpty{color: white !important;}


.listOfTodos::-webkit-scrollbar {
  background-color: aquamarine;
  display: none;
}

.activated {
  border: 2px solid yellow;
}

@keyframes listLoadingAnimation{
  0%{
      background-position: -350px;
  }
  100%{background-position: 350px;}

}


.loadingList{
  max-height: 280px;
  min-height: 250px;
  min-width: 280px;
  width: 350px;  
  background: linear-gradient(90deg,rgba(255, 255, 255, 0) 15% ,white,rgba(255, 255, 255, 0) 45%);
  border-radius: 20px;
  border: none;
  margin-top: 25px;
  padding: 10px 0 10px 0;
  animation-name: listLoadingAnimation;
animation-duration: 2s;
animation-iteration-count: infinite;
animation-timing-function: linear;
}

@media (max-width:411px){
  .listOfTodos{width:90%;}
  .loadingList{min-width: 90%;}
  
  }